import styles from './SegundaPantalla.module.scss';
import React, { useEffect } from 'react';
import { motion } from 'framer-motion';
import { SPFI } from '@pnp/sp';
import Button from '../../../../components/ui/Button/Button';
import { useFondo } from '../../../../_context/FondoContext';

interface SegundaPantallaProps {
  isDarkTheme: boolean;
  sp: SPFI;
  onAbrirModal: () => void;
}

const SegundaPantalla: React.FC<SegundaPantallaProps> = ({ isDarkTheme, onAbrirModal }) => {
const { setFondoActivo } = useFondo();
  useEffect(() => {
    console.log("SegundaPantalla cargada");
    setFondoActivo('escalerafijaSegundaPantalla');
  }, [setFondoActivo]);

  return (
    <div className={styles.container}>
      <div className={styles.content}>
        <motion.h2
          className={isDarkTheme ? styles.textLight : styles.textDark}
          initial={{ opacity: 0, y: -30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 1 }}
        >
          Nuestra Metodolog√≠a de Ense√±anza
        </motion.h2>

        <motion.p
          className={isDarkTheme ? styles.textLight : styles.textDark}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1.5 }}
        >
          En este curso interactivo, aprender√°s mediante recursos visuales, ejemplos reales y din√°micas de participaci√≥n activa.
        </motion.p>

        <Button onClick={onAbrirModal}>
          + info
        </Button>
      </div>
    </div>
  );
};

export default SegundaPantalla;